import { toast } from "react-toastify";

export const Toast = (msg: string, type: boolean, time: number = 2500) =>
  type
    ? toast.success(msg, {
        position: "bottom-left",
        autoClose: time,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      })
    : toast.error(msg, {
        position: "bottom-left",
        autoClose: time,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
