import axios from "axios";
import React, { useEffect, useState } from "react";
import "./confirm-user.style.scss";
// import { Grid, CssBaseline, Paper, Hidden } from "@material-ui/core";
// import Logo from "../../assets/image/Logo.png";
// import { Link } from "react-router-dom";
// import { useCookies } from "react-cookie";
// import { Button, Col, Row } from "react-bootstrap";
import { Toast } from "./../../components/toaster/toaser";

const ConfirmUser = ({ match: { params }, history: { push } }: any) => {
  const [response, setResponse] = useState("جارٍ تأكيد الحساب ...");
  useEffect(() => {
    // console.log(params);
    axios
      .post(`/confirmemail/${params.id}/${params.code}`)
      .then(({ data }) => {
        if (data?.isConfirmed) {
          push("/account/signin");
          Toast("تم تأكيد الحساب بنجاح", true);
        }
      })
      .catch((err) => {
        Toast("حدث خطأ ما", false);
      });
    // push("/account/signin");
  }, []);
  return <div className="confirmed-success">{response}</div>;
};

export default ConfirmUser;
